import { v4 as uuidv4 } from "uuid";
import * as events from "../events";
import moment from "moment-timezone";
import { getUser, API_URL } from "../../constants/Config";

const setUser = (user) => {
  if (user) {
    // console.lo
    const subscription = user.lastsub; //subscribed && user.subscribed[user.subscribed.length - 1];

    // console.log("subscription", subscription, user.plan);
    const freetrial = user.freetrial && user.freetrial;

    localStorage.setItem(
      "@shadowuser",
      JSON.stringify({
        _id: user._id,
        team: user.team,
        owner: user._id === user.team,
        token: user.token,
        pic: user.pic ? user.pic.id : 0,
        email: user.email,
        firstname: user.firstname,
        invitecode: user.invitecode,
        lastname: user.lastname,
        name: user.firstname + " " + user.lastname,
        joined: user.created,
        customerId: user.customerId,
        pm: user.pm,
        role: user.role,
        freetrial,
        sub: {
          start: (subscription && subscription.start) || null,
          end: (subscription && subscription.end) || null,
          plan: (user.plan && user.plan.type) || null,
        },
        price: {
          mail: (freetrial && freetrial.mail) || 0,
          smartsearch: (freetrial && freetrial.smartsearch) || 0,
        },
      })
    );
  }
};

export const login = (email, password) => {
  return async (dispatch) => {
    try {
      const response = await fetch(`${API_URL}/login`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          email: email,
          password: password,
        }),
      });

      if (!response.ok) {
        const resError = await response.json();
        console.log("Response Error", resError.error);

        if (resError && resError.error) {
          throw new Error(resError.error);
        } else {
          throw new Error("Something went wrong!");
        }
      }

      const resData = await response.json();
      // console.log(resData.data);

      const user = resData.data;
      var d = new Date();
      d.setFullYear(d.getFullYear() + 5);
      const expirationDate = d;

      setUser(user);

      await dispatch(authenticate(user._id, user.token, expirationDate));

      await dispatch(getProfile());

      // await dispatch(planActions.fetchPlans());

      // await dispatch(campaignsActions.fetchCampaigns());

      // await dispatch(templatesActions.fetchTemplates());

      // await dispatch(signatureActions.fetchSignatures());

      // await dispatch(mapsActions.fetchStateProperties());

      // await dispatch(mapsActions.fetchZipProperties());
      // await dispatch(propertiesActions.fetchStatus());

      // await dispatch(propertiesActions.fetchTags());

      // await dispatch(pipelineActions.fetchPipelines());

      // await dispatch(getFaqs());
    } catch (err) {
      throw new Error(err.message);
    }
  };
};

export const forgotPassword = (email) => {
  return async (dispatch) => {
    const response = await fetch(`${API_URL}/forgot`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        email,
      }),
    });

    if (!response.ok) {
      const errRes = await response.json();
      let message = "Something went wrong!";
      if (errRes.error) {
        message = errRes.error;
      }
      throw new Error(message);
    }

    const resData = await response.json();
    if (resData.error) {
      throw new Error(resData.error);
    }
    // console.log("ResData", resData);
    return resData.success;
  };
};

export const resetPassword = (data) => {
  return async (dispatch) => {
    const response = await fetch(`${API_URL}/reset`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data),
    });

    if (!response.ok) {
      const errRes = await response.json();
      let message = "Something went wrong!";
      if (errRes.error) {
        message = errRes.error;
      }
      throw new Error(message);
    }

    const resData = await response.json();
    return resData.success;
  };
};

export const authenticate = (userId, token) => {
  return (dispatch) => {
    dispatch({
      type: events.AUTHENTICATE,
      userId: userId,
      token: token,
    });
  };
};

export const signup = async (data) => {
  const url = `${API_URL}/register`;
  const response = await fetch(url, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify({
      ...data,
      os: "W",
      device_id: uuidv4(),
    }),
  });

  if (!response.ok) {
    const resError = await response.json();
    console.log("Response Error", resError.error);

    if (resError && resError.error) {
      throw new Error(resError.error);
    } else {
      throw new Error("Something went wrong!");
    }
  }
  const resData = await response.json();
  return resData.data;
};

export const getProfile = (check = false) => {
  return async (dispatch, getState) => {
    try {
      const { _id, token } = getUser();
      const response = await fetch(`${API_URL}/users/${_id}`, {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
      });

      if (!response.ok) {
        const resError = await response.json();
        // console.log("Response Error", resError.error);
        if (resError && resError.error) {
          if (resError.error === "token expired or invalid token") {
            localStorage.removeItem("@shadowuser");
          }
          throw new Error(resError.error);
        } else {
          throw new Error("Something went wrong!");
        }
      } else {
        const resData = await response.json();
        const user = resData.data;
        if (!check) {
          const appId = uuidv4();
          dispatch({
            type: events.SET_PROFILE,
            data: { ...user, appId },
          });

          dispatch({
            type: events.SET_PROPERTY_FILTER,
            data: {
              fromDate: moment(user.created).format("YYYY-MM-DD"),
              isUserFilter: false,
              refresh: true,
            },
          });
        }
        if (user) {
          const xuser = getUser();
          if (xuser) {
            setUser({ ...xuser, ...user });
          }
        }
        return user;
      }
    } catch (err) {
      // console.log(err);
      throw new Error(err.message);
      // return null;
    }
  };
};

export const updateProfile = (data, socket = null) => {
  return async (dispatch, getState) => {
    const { token } = getUser();
    const { _id } = data;
    delete data._id;
    const body = JSON.stringify(data);
    const response = await fetch(`${API_URL}/users/${_id}`, {
      method: "PATCH",
      headers: {
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      },
      body,
    });

    if (!response.ok) {
      const errorResData = await response.json();
      let message = "Something went wrong!";
      console.log(errorResData);
      if (errorResData.error) {
        throw new Error(errorResData.error);
      } else {
        throw new Error(message);
      }
    }

    const resData = await response.json();
    if (resData.data) {
      // console.log("RESDATA", resData.data);
      const user = getUser();
      setUser({ ...user, ...resData.data });
      if (socket) {
        if (data.changerole) {
          socket.emit("user:role:changed", data);
        }
        if (data.changeactive) {
          socket.emit("user:status:changed", data);
        }
      }
      dispatch({
        type: events.SET_PROFILE,
        data: resData.data,
      });
    }
  };
};

export const uploadProfilePicture = (picture, setProgress) => {
  return async (dispatch, getState) => {
    // any async code you want!
    const token = getState().auth.token;
    const profile = getState().auth.profile;
    const { availableProperties } = getState().properties;
    const formData = new FormData();

    formData.append("_id", profile._id);
    formData.append("picture", {
      type: "image/jpg",
      uri: picture,
      name: "user_" + profile.id + ".jpg",
    });
    var xhr = new XMLHttpRequest();
    xhr.open("POST", `${API_URL}/users/upload/picture`, true);
    xhr.addEventListener(
      "progress",
      function (e) {
        var done = e.position || e.loaded,
          total = e.totalSize || e.total;
        dispatch({
          type: events.SET_PROGRESS,
          progress: Math.floor((done / total) * 1000) / 10,
        });
        // console.log(
        //   "xhr progress: " + Math.floor((done / total) * 1000) / 10 + "%"
        // );
      },
      false
    );
    if (xhr.upload) {
      xhr.upload.onprogress = function (e) {
        var done = e.position || e.loaded,
          total = e.totalSize || e.total;
        // console.log(
        //   "xhr.upload progress: " +
        //     done +
        //     " / " +
        //     total +
        //     " = " +
        //     Math.floor((done / total) * 1000) / 10 +
        //     "%"
        // );
        setProgress(Math.floor((done / total) * 1000) / 10);
      };
    }
    xhr.setRequestHeader("Content-Type", "multipart/form-data");
    xhr.setRequestHeader("Authorization", "Bearer " + token);
    xhr.onreadystatechange = async function () {
      if (this.readyState === 4 && this.status === 200) {
        const resData = JSON.parse(this.responseText);
        if (resData.data) {
          // await CacheManager.addToCache({
          //   file: picture,
          //   key: `pp-${profile._id}`,
          // })
          dispatch({
            type: events.SET_PROFILE,
            data: resData.data,
          });

          const properties = availableProperties.map((p) => {
            const u = resData.data;
            if (p.createdUser && p.createdUser.length > 0) {
              let cu = p.createdUser[0];
              if (cu._id === u._id) {
                cu.pic = u.pic;
                p.createdUser = [cu];
              }
            }
            return p;
          });
          dispatch({
            type: events.SET_PROPERTIES,
            properties,
          });
        } else {
          if (resData.error) {
            throw new Error(resData.error);
          }
        }
      }
    };
    xhr.send(formData);
  };
};

export const getFaqs = () => {
  return async (dispatch, getState) => {
    const { token } = getUser();
    try {
      const response = await fetch(`${API_URL}/faqs/get`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
        body: JSON.stringify({
          skip: 0,
          filter: { active: true },
        }),
      });

      if (!response.ok) {
        const resError = await response.json();
        console.log("Response Error", resError);

        if (resError && resError.error) {
          throw new Error(resError.error);
        } else {
          throw new Error("Something went wrong!");
        }
      }
      const resData = await response.json();
      const faqs = resData.data;

      dispatch({
        type: events.SET_FAQS,
        data: faqs,
      });
    } catch (err) {
      console.log(err);
    }
  };
};
