import React, { useState } from "react";
import { HStack, Text, VStack, ScrollView, Spinner, Center } from "native-base";
import { useForm } from "react-hook-form";
import { useDispatch } from "react-redux";
import moment from "moment-timezone";

import Card from "./Card";
import Util from "../../../util";
import ShadowInput from "../../../components/ShadowInput";
import * as propertiesActions from "../../../store/actions/properties";

const { vw, vh } = Util;

const Notes = ({ property, _showAlert }) => {
  const dispatch = useDispatch();
  const [isOpen, setIsOpen] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const {
    control,
    handleSubmit,
    formState: { errors },
    reset,
    getValues,
  } = useForm({
    mode: "all",
    defaultValues: {
      note: "",
    },
  });

  const onSubmit = async (data) => {
    try {
      let postdata = {
        _id: property._id,
        note: data.note,
      };
      const action = propertiesActions.updatePostCardName(postdata);
      setIsLoading(true);
      await dispatch(action);
      setIsLoading(false);
      _showAlert("success", "Note saved successfully.");
      reset();
    } catch (err) {
      _showAlert("error", err.message);
      setIsLoading(false);
    }
  };

  const NoteItem = ({ item }) => {
    return (
      <VStack style={{ paddingVertical: 5 }}>
        <Text
          style={{
            color: "rgba(0,0,0,1)",
            fontSize: 14,
            fontFamily: "Poppins",
          }}
        >
          {item.note}
        </Text>
        <Text
          style={{
            color: "rgba(0,0,0,0.5)",
            fontSize: 11,
            fontFamily: "Poppins",
          }}
        >
          {item.createdname +
            " - " +
            moment(item.created).format("MM/DD/YYYY - hh:mm A")}
        </Text>
      </VStack>
    );
  };

  const NoteList = ({ notes }) => {
    return (
      <ScrollView marginBottom={5} maxHeight={200}>
        {notes &&
          notes.length > 0 &&
          notes.map((item, idx) => <NoteItem key={"n-" + idx} item={item} />)}
      </ScrollView>
    );
  };

  return (
    <>
      <Card
        title='Notes'
        // onPress={() => {
        //   // setIsOpen(true);
        // }}
      >
        <HStack>
          <ShadowInput
            isRequired
            name='note'
            // label={"Owner Name "}
            placeholder='Type your note and hit return'
            control={control}
            rules={{
              required: true,
              message: "Please enter your note",
            }}
            onSubmitEditing={(e) => {
              handleSubmit(onSubmit)();
            }}
            style={{ marginTop: vh(20) }}
            invalid={errors.note ? true : false}
            error={errors.note ? errors.note.message : ""}
            inputStyle={{}}
          />
          {isLoading && <Spinner marginLeft={vw(-30)} marginTop={vh(10)} />}
        </HStack>
        <NoteList notes={property.notes} />
      </Card>
    </>
  );
};

export default Notes;
