import React, { useState, useEffect } from "react";
import {
  FlatList,
  Text,
  Pressable,
  Center,
  CheckCircleIcon,
  VStack,
  HStack,
  Checkbox,
} from "native-base";
import { useForm } from "react-hook-form";
import { useDispatch } from "react-redux";

import Card from "./Card";
import { vw, vh } from "../../../util";
import ShadowModal from "../../../components/ShadowModal";
import ShadowInput from "../../../components/ShadowInput";
import ShadowButton from "../../../components/ShadowButton";

import * as propertiesActions from "../../../store/actions/properties";
import Colors from "../../../constants/Colors";
import ShadowSearch from "../../../components/ShadowSearch";

const Address = ({ property, _showAlert }) => {
  const dispatch = useDispatch();
  const [isOpen, setIsOpen] = useState(false);
  const [editAddress, setEditAddress] = useState({});
  const [address, setAddress] = useState(null);
  const [refresh, setRefresh] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const {
    control,
    handleSubmit,
    formState: { errors },
    reset,
    setValue,
    getValues,
    trigger,
  } = useForm({
    mode: "all",
    defaultValues: {
      ...editAddress,
    },
  });

  const onSubmit = async (data) => {
    try {
      const ff =
        data.address1.trim() +
        ", " +
        data.city.trim() +
        ", " +
        data.state.trim() +
        " " +
        data.zip.trim();

      let address = {
        type: "user",
        address1: data.address1.trim(),
        address2: data.address2 ? data.address2.trim() : "",
        city: data.city.trim(),
        state: data.state.trim(),
        zip: data.zip.trim(),
        ff,
        fulladdress: ff,
      };

      let msg = `Postal address ${ff} saved successfully.`;

      if (editAddress && editAddress.addid) {
        msg = `Postal address ${ff} updated successfully.`;
        address.addid = editAddress.addid;
        if (data.default) {
          address.default = true;
          address.name = property.postcardname || property.name;
          const de = property.addresses.find((e) => e.default === true);
          if (de && de.addid !== address.edit) {
            address.defaultAddress = de;
          }
        } else {
          address.default = editAddress.default;
        }
      } else {
        if (property.addresses && property.addresses.length === 0) {
          address.default = true;
        } else {
          if (data.default) {
            address.default = true;
            address.name = property.postcardname || property.name;
            const de = property.addresses.find((e) => e.default === true);
            if (de && de.addid !== address.edit) {
              address.defaultAddress = de;
            }
          } else {
            address.default = false;
          }
        }
      }

      let postdata = {
        _id: property._id,
        addresses: [address],
      };

      const action = propertiesActions.saveMailingAddress(postdata);

      setIsLoading(true);
      await dispatch(action);
      setIsLoading(false);
      _showAlert("success", msg);
      setIsOpen(false);
    } catch (err) {
      _showAlert("error", err.message);
      setIsLoading(false);
    }
  };

  const AddressItem = ({ item }) => {
    // console.log(item)

    useEffect(() => {
      if (refresh && address && address["shortaddress"]) {
        const shortaddress = address["shortaddress"];
        setValue("address1", shortaddress["address1"]);
        trigger("address1");
        setValue("address2", shortaddress["address2"]);
        trigger("address2");
        setValue("city", shortaddress["city"]);
        trigger("city");
        setValue("state", shortaddress["state"]);
        trigger("state");
        setValue("zip", shortaddress["postcode"]);
        trigger("zip");
        setRefresh(false);
      }
    }, []);

    return (
      <Pressable
        onPress={() => {
          setIsOpen(true);
          reset();
          setValue("address1", item.address1);
          setValue("address2", item.address2);
          setValue("city", item.city);
          setValue("state", item.state);
          setValue("zip", item.zip);
          setEditAddress(item);
        }}
        style={{
          backgroundColor: "white",
          height: vh(60),
          borderRadius: 5,
          padding: vh(10),
          marginRight: vw(5),
          marginVertical: vh(10),
          borderColor: Colors.primary,
          borderWidth: 1,
        }}
      >
        <Text
          style={{
            fontFamily: "Poppins_medium",
            fontSize: vw(15),
            color: "rgba(0,0,0,0.8)",
          }}
        >
          {item.ff}
        </Text>
        <Text
          style={{
            fontFamily: "Poppins_medium",
            fontSize: vw(13),
            color: "rgba(0,0,0,0.5)",
          }}
        >
          ({item.type || "user added"})
        </Text>
        {item.default && (
          <Center
            rounded={"full"}
            style={{
              position: "absolute",
              right: -5,
              top: -5,
              // borderColor: "white",
              // borderWidth: 1,
              backgroundColor: "white",
            }}
          >
            <CheckCircleIcon color={Colors.primary} />
          </Center>
        )}
      </Pressable>
    );
  };

  return (
    <>
      <Card
        title='Postal Address'
        onPress={() => {
          reset();
          setEditAddress({});
          setIsOpen(true);
        }}
      >
        <Text>Selected address will be used for the campaign.</Text>
        <FlatList
          ListEmptyComponent={
            <Center w={"90%"} style={{ flexDirection: "row" }}>
              <Text
                w={"90%"}
                style={{
                  textAlign: "center",
                  flex: 1,
                  flexWrap: "wrap",
                  flexShrink: 1,
                }}
              >
                No postal addresses found, add one to run the campaign
              </Text>
            </Center>
          }
          horizontal={true}
          data={property.addresses}
          renderItem={({ item }) => (
            <AddressItem item={item} selected={item.default ? true : false} />
          )}
          keyExtractor={(item, index) => index.toString()}
          scrollEnabled={property.addresses && property.addresses.length > 1}
          // ListFooterComponent={() => (
          //   <FooterItem
          //     navigation={props.navigation}
          //     navigate={'AddMailingAddress'}
          //     data={null}
          //     propertyId={property.property_detail.id}
          //   />
          // )}
        />
      </Card>
      <ShadowModal
        placement={"top"}
        size={"lg"}
        header='Postal Address'
        isOpen={isOpen}
        close={() => {
          reset();
          setIsOpen(false);
        }}
        buttons={
          <HStack space={2}>
            <ShadowButton
              space={2}
              label='Cancel'
              variant='outline'
              onPress={() => {
                reset();
                setIsOpen(false);
              }}
              style={{
                height: vh(36),
              }}
            />
            <ShadowButton
              space={3}
              isLoading={isLoading}
              label='Save'
              onPress={handleSubmit(onSubmit)}
              style={{
                height: vh(36),
              }}
            />
          </HStack>
        }
      >
        <VStack
          paddingHorizontal={vw(10)}
          marginTop={vh(-20)}
          marginBottom={vh(20)}
          space={6}
        >
          {editAddress.type !== "owner" && editAddress.type !== "property" && (
            <ShadowSearch setAddress={setAddress} setRefresh={setRefresh} />
          )}
          <ShadowInput
            isReadOnly={
              editAddress.type === "owner" || editAddress.type === "property"
            }
            isRequired
            name='address1'
            label={"Address1 "}
            placeholder='Enter address 1'
            control={control}
            rules={{
              required: true,
              message: "Please enter address 1",
            }}
            style={{ marginTop: vh(-20) }}
            invalid={errors.address1 ? true : false}
            error={errors.address1 ? errors.address1.message : ""}
            inputStyle={{}}
          />
          <ShadowInput
            isReadOnly={
              editAddress.type === "owner" || editAddress.type === "property"
            }
            name='address2'
            label={"Address2 "}
            placeholder='Enter address2'
            control={control}
            rules={
              {
                // required: true,
                // message: "Please enter address2",
              }
            }
            invalid={errors.address2 ? true : false}
            error={errors.address2 ? errors.address2.message : ""}
            inputStyle={{}}
          />
          <HStack space={4} justifyContent={"space-between"}>
            <ShadowInput
              isReadOnly={
                editAddress.type === "owner" || editAddress.type === "property"
              }
              style={{ width: "48%" }}
              isRequired
              name='city'
              label={"City "}
              placeholder='Enter city'
              control={control}
              rules={{
                required: true,
                message: "Please enter city",
              }}
              invalid={errors.city ? true : false}
              error={errors.city ? errors.city.message : ""}
              inputStyle={{}}
            />
            <HStack
              space={4}
              style={{ width: "48%" }}
              justifyContent={"space-between"}
            >
              <ShadowInput
                isReadOnly={
                  editAddress.type === "owner" ||
                  editAddress.type === "property"
                }
                style={{ width: "48%" }}
                isRequired
                name='state'
                label={"State "}
                placeholder='Enter state'
                control={control}
                rules={{
                  required: true,
                  message: "Please enter state",
                }}
                invalid={errors.state ? true : false}
                error={errors.state ? errors.state.message : ""}
                inputStyle={{}}
              />
              <ShadowInput
                isReadOnly={
                  editAddress.type === "owner" ||
                  editAddress.type === "property"
                }
                style={{ width: "48%" }}
                isRequired
                maxLength={5}
                minLength={5}
                name='zip'
                label={"Zipcode "}
                placeholder='Enter zip'
                control={control}
                rules={{
                  // valueAsNumber: true,
                  pattern: {
                    value: /^[0-9]{5}?$/,
                    message: "Invalid zipcode",
                  },
                  required: {
                    value: true,
                    message: "Please enter zipcode",
                  },
                  maxLength: {
                    value: 5,
                    message: "Invalid zipcode",
                  },
                  minLength: {
                    value: 5,
                    message: "Invalid zipcode",
                  },
                }}
                invalid={errors.zip ? true : false}
                error={errors.zip ? errors.zip.message : ""}
                inputStyle={{}}
              />
            </HStack>
          </HStack>
          {!editAddress.default && (
            <HStack
              style={{
                marginTop: vh(10),
                justifyContent: "flex-start",
                zIndex: 9999,
              }}
            >
              <Checkbox
                name='default'
                onChange={(v) => {
                  setValue("default", v);
                  trigger("default");
                }}
                isChecked={getValues("default") || false}
              />
              <Text
                style={{
                  marginLeft: vw(10),
                  fontSize: vw(15),
                  fontFamily: "Poppins",
                  color: "rgba(0,0,0,0.8)",
                }}
              >
                Default
              </Text>
            </HStack>
          )}
        </VStack>
      </ShadowModal>
    </>
  );
};

export default Address;
