import React, { useState } from "react";
import {
  HStack,
  Text,
  FlatList,
  Pressable,
  Checkbox,
  Center,
  CheckCircleIcon,
} from "native-base";
import { useForm } from "react-hook-form";
import { useDispatch } from "react-redux";

import Card from "./Card";
import Util from "../../../util";
import ShadowModal from "../../../components/ShadowModal";
import ShadowInput from "../../../components/ShadowInput";
import ShadowButton from "../../../components/ShadowButton";
import * as propertiesActions from "../../../store/actions/properties";
import Colors from "../../../constants/Colors";

const { vw, vh } = Util;

const Emails = ({
  property,
  _showAlert,
  searching,
  setSearching,
  doSmartSearch,
}) => {
  const dispatch = useDispatch();
  const [isOpen, setIsOpen] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [editEmail, setEditEmail] = useState({});
  const emails = property && property.emails ? property.emails : [];
  const {
    control,
    handleSubmit,
    formState: { errors },
    reset,
    getValues,
    setValue,
    trigger,
  } = useForm({
    mode: "all",
    defaultValues: { ...editEmail },
  });

  const onSubmit = async (data) => {
    try {
      let email = {
        label: data.label,
        email: data.email,
      };
      let msg = data.label + " Email Address saved successfully!"
      if (editEmail && editEmail.email) {
        msg = data.label + " Email Address updated successfully!"
        email.edit = editEmail.email;
        email.smartsearch = editEmail.smartsearch;
        if (data.default) {
          email.default = true;
          const de = emails.find((e) => e.default === true);
          if (de && de.email !== email.edit) {
            email.defaultEmail = de.email;
          }
        }
      } else {
        if (emails && emails.length === 0) {
          email.default = true;
        }
      }

      let postdata = {
        _id: property._id,
        emails: [email],
      };

      const action = propertiesActions.saveEmails(postdata);
      setIsLoading(true);

      await dispatch(action);
      _showAlert("success", msg);
      setIsLoading(false);
      reset();
      setIsOpen(false);
    } catch (err) {
      _showAlert("error", err.message);
      setIsLoading(false);
    }
  };

  const EmailItem = (props) => {
    return (
      <Pressable
        onHoverIn={() => {}}
        onHoverOut={() => {}}
        onPress={() => {
          if (props.data.default && props.data.smartsearch) {
            return;
          }
          setIsOpen(true);
          setValue("email", props.email);
          setValue("label", props.label);
          setValue("default", props.data.default);
          setEditEmail(props.data);
        }}
        _web={{
          backgroundColor: "white",
        }}
        style={{
          borderRadius: 10,
          height: vh(48),
          margin: vh(10),
          paddingTop: vh(5),
          borderColor: Colors.primary,
          borderWidth: 1,
          paddingHorizontal: vw(10),
        }}
      >
        <Text
          style={{
            fontFamily: "Poppins_medium",
            fontSize: vw(13),
            color: "#303030",
          }}
        >
          {props.email}
        </Text>
        <Text
          style={{
            fontFamily: "Poppins",
            fontSize: vw(11),
            color: "#303030",
          }}
        >
          {props.label}
        </Text>
        {props.data.default && (
          <Center
            rounded={"full"}
            style={{
              position: "absolute",
              right: -5,
              top: -5,
              // borderColor: "white",
              // borderWidth: 1,
              backgroundColor: "white",
            }}
          >
            <CheckCircleIcon color={Colors.primary} />
          </Center>
        )}
      </Pressable>
    );
  };  

  return (
    <>
      <Card
        title='Emails'
        onPress={() => {
          setEditEmail({});
          reset();
          setIsOpen(true);
        }}
      >
        <FlatList
          ListEmptyComponent={
            !property.smartsearch ? null : (
              <Text style={{ marginVertical: vh(10) }}>
                No emails found with Smart Search
              </Text>
            )
          }
          scrollEnabled={emails.length > 1}
          ListHeaderComponent={
            !property.smartsearch ? (
              <ShadowButton
                onPress={doSmartSearch}
                isLoading={searching}
                label='Smart Search'
                style={{ marginVertical: vh(10) }}
              />
            ) : null
          }
          // style={{ marginHorizontal: 12 }}
          horizontal={true}
          data={emails.filter((p) => p.email !== "")}
          renderItem={({ item }) => {
            return (
              <EmailItem
                data={item}
                email={item.email}
                label={item.label}
                propertyId={property._id}
                smartsearch={item.smartsearch}
              />
            );
          }}
          keyExtractor={(item, index) => index.toString()}
        />
      </Card>
      <ShadowModal
        bodyHeight={vh(80)}
        size='lg'
        header='Email'
        isOpen={isOpen}
        close={() => {
          reset();
          setIsOpen(false);
        }}
        buttons={
          <HStack space={2}>
            <ShadowButton
              space={2}
              color={"rgba(0,0,0, 0.5 )"}
              label='Cancel'
              variant='outline'
              onPress={() => {
                reset();
                setIsOpen(false);
              }}
            />
            <ShadowButton
              space={3}
              isLoading={isLoading}
              label='Save'
              onPress={handleSubmit(onSubmit)}
            />
          </HStack>
        }
      >
        <HStack
          style={{
            justifyContent: "space-between",
            zIndex: 9999,
            marginTop: vh(-20),
          }}
          space={4}
        >
          <ShadowInput
            isReadOnly={editEmail && editEmail.smartsearch}
            style={{ width: "48%" }}
            isRequired
            name='label'
            label={"Label "}
            placeholder='Enter email label'
            control={control}
            rules={{
              required: true,
              message: "Please enter email label",
            }}
            invalid={errors.label ? true : false}
            error={errors.label ? errors.label.message : ""}
            inputStyle={{}}
          />
          <ShadowInput
            isReadOnly={editEmail && editEmail.smartsearch}
            style={{ width: "48%" }}
            isRequired
            name='email'
            label={"Email "}
            placeholder='Enter email'
            control={control}
            rules={{
              required: true,
              message: "Please enter valid email address",
              pattern: {
                value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                message: "invalid email address",
              },
            }}
            invalid={errors.email ? true : false}
            error={errors.email ? errors.email.message : ""}
            inputStyle={{}}
          />
        </HStack>
        {!editEmail.default && (
          <HStack
            style={{
              marginTop: vh(40),
              width: vw(300),
              justifyContent: "flex-start",
              zIndex: 9999,
            }}
          >
            <Checkbox
              name='default'
              onChange={(v) => {
                console.log(v);
                setValue("default", v);
                trigger("default");
              }}
              isChecked={getValues("default") || false}
            />
            <Text
              style={{
                marginLeft: vw(10),
                fontSize: vw(15),
                fontFamily: "Poppins",
                color: "rgba(0,0,0,0.8)",
              }}
            >
              Default
            </Text>
          </HStack>
        )}
      </ShadowModal>
    </>
  );
};

export default Emails;
