import React, { useState, useCallback, useEffect, useRef } from "react";
import {
  HStack,
  Pressable,
  VStack,
  Text,
  useToast,
  Hidden,
  Spinner,
} from "native-base";
import { useParams, useNavigate } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";

import { vw, vh } from "../../../util";
import NavBar from "../home/NavBar";
import Colors from "../../../constants/Colors";
import * as propertiesActions from "../../../store/actions/properties";
import * as campaignsActions from "../../../store/actions/campaigns";
import * as authActions from "../../../store/actions/auth";
import Attributes from "./Attributes";
import Info from "./Info";
import Campaign from "./Campaign";

const useMountedState = () => {
  const mountedRef = useRef(false);
  const isMounted = useCallback(() => mountedRef.current, []);

  useEffect(() => {
    mountedRef.current = true;

    return () => {
      mountedRef.current = false;
    };
  }, []);

  return isMounted;
};

const Details = ({ user, setLogout }) => {
  const navigate = useNavigate();
  let { id } = useParams();
  const toast = useToast();
  const [tab, setTab] = useState(0);
  const [searching, setSearching] = useState(false);
  const dispatch = useDispatch();
  const isMounted = useMountedState();
  const { fetchedProperty: property, statuslist: status } = useSelector(
    (state) => state.properties
  );
  const tagslist = useSelector((state) => state.properties.tags);

  const _showAlert = useCallback((status, msg) => {
    toast.closeAll();
    toast.show({
      duration: 2000,
      placement: "top",
      render: () => {
        let bg = "red.300";
        if (status === "error") {
          bg = "red.300";
        } else if (status === "success") {
          bg = "green.300";
        } else if (status === "info") {
          bg = "blueGray.300";
        } else if (status === "warning") {
          bg = "orange.300";
        }
        return (
          <Text
            style={{
              fontSize: vw(12),
              fontFamily: "Poppins_medium",
              color: "#33214B",
            }}
            p={2}
            bg={bg}
            rounded={"full"}
          >
            {msg}
          </Text>
        );
      },
    });
  }, [toast]);

  const loadProperty = useCallback(async () => {
    try {
      await dispatch(propertiesActions.fetchStatus());
      await dispatch(propertiesActions.fetchTags());
      await dispatch(campaignsActions.fetchCampaigns());
      await dispatch(propertiesActions.fetchPropertyById(id));
    } catch (err) {
      _showAlert("error", "something went wrong.");
    }
  }, [dispatch, _showAlert, id]);

  useEffect(() => {
    if (isMounted) {
      // setIsLoading(true);
      loadProperty().then(() => {
        // setIsLoading(false);
      });
    }
  }, [dispatch, loadProperty, isMounted]);

  const doSmartSearch = async () => {
    try {
      setSearching(true);
      const action = propertiesActions.doSmartSearch(property._id);
      await dispatch(action);
      await dispatch(authActions.getProfile());
      setSearching(false);
      // setSmartSearchMessage(true);
    } catch (err) {
      _showAlert("error", err.message);
      setSearching(false);
    }
  };

  const ph = 100 - parseFloat(vh(70)) + "vh";

  return (
    <VStack style={{ minHeight: "100vh" }} bg={"#F7F8FA"}>
      <NavBar user={user} setLogout={setLogout} />

      {property ? (
        <VStack style={{ height: vh(ph) }}>
          <VStack
            style={{
              // height: vh(100),
              marginTop: vh(-10),
              marginHorizontal: vw(20),
              borderBottomColor: "rgba(0,0,0,0.15)",
              borderBottomWidth: 1,
              justifyContent: "space-between",
              zIndex: 99999,
              paddingBottom: vh(10),
            }}
          >
            <Hidden only={["sm", "base"]}>
              <HStack justifyContent={"space-between"}>
                <Text
                  style={{
                    fontSize: vw(24),
                    marginTop: vh(30),
                    color: "#303030",
                    fontFamily: "Poppins_medium",
                  }}
                >
                  Property #{property.tid}
                </Text>
                <Text
                  style={{
                    fontSize: vw(24),
                    marginTop: vh(30),
                    color: "#303030",
                    fontFamily: "Poppins_medium",
                  }}
                >
                  {property.propertyaddress}
                </Text>
              </HStack>
            </Hidden>
            <Hidden from='md'>
              <VStack>
                <Text
                  style={{
                    fontSize: vw(24),
                    marginTop: vh(10),
                    color: "#303030",
                    fontFamily: "Poppins_medium",
                    textAlign: "center",
                  }}
                >
                  Property #{property.tid}
                </Text>
                <Text
                  style={{
                    fontSize: vw(24),
                    marginTop: vh(10),
                    color: "#303030",
                    fontFamily: "Poppins_medium",
                    textAlign: "center",
                  }}
                >
                  {property.propertyaddress}
                </Text>
              </VStack>
            </Hidden>
          </VStack>
          <HStack marginTop={vh(45)} justifyContent={"space-between"}>
            <VStack
              style={{
                width: vw(255),
                height: vh(500),
                borderRightWidth: vw(1),
                borderRightColor: "rgba(0,0,0, 0.15)",
                // position:"absolute"
              }}
            >
              <Pressable
                style={{
                  height: vh(40),
                  borderRightWidth: tab === 0 ? vw(3) : 0,
                  borderRightColor: Colors.primary,
                  paddingHorizontal: vw(40),
                }}
                onPress={() => {
                  setTab(0);
                }}
              >
                <Text
                  style={{
                    fontFamily: tab === 0 ? "Poppins_medium" : "Poppins",
                    fontSize: vw(15),
                    color: tab === 0 ? Colors.primary : "#686868",
                    height: vh(40),
                    lineHeight: vh(40),
                  }}
                >
                  Info
                </Text>
              </Pressable>
              <Pressable
                style={{
                  marginTop: vh(20),
                  height: vh(40),
                  borderRightWidth: tab === 1 ? vw(3) : 0,
                  borderRightColor: Colors.primary,
                  paddingHorizontal: vw(40),
                }}
                onPress={() => {
                  setTab(1);
                }}
              >
                <Text
                  style={{
                    fontFamily: tab === 1 ? "Poppins_medium" : "Poppins",
                    fontSize: vw(15),
                    color: tab === 1 ? Colors.primary : "#686868",
                    height: vh(40),
                    lineHeight: vh(40),
                  }}
                >
                  Property
                </Text>
              </Pressable>
              <Pressable
                style={{
                  marginTop: vh(20),
                  height: vh(40),
                  borderRightWidth: tab === 2 ? vw(3) : 0,
                  borderRightColor: Colors.primary,
                  paddingHorizontal: vw(40),
                }}
                onPress={() => {
                  setTab(2);
                }}
              >
                <Text
                  style={{
                    fontFamily: tab === 2 ? "Poppins_medium" : "Poppins",
                    fontSize: vw(15),
                    color: tab === 2 ? Colors.primary : "#686868",
                    height: vh(40),
                    lineHeight: vh(40),
                  }}
                >
                  Campaign
                </Text>
              </Pressable>
              <Pressable
                style={{
                  marginTop: vh(20),
                  height: vh(40),
                  paddingHorizontal: vw(40),
                }}
                onPress={() => {
                  navigate(-1);
                }}
              >
                <Text
                  style={{
                    fontFamily: "Poppins",
                    fontSize: vw(15),
                    color: "#686868",
                    height: vh(40),
                    lineHeight: vh(40),
                  }}
                >
                  Back to list
                </Text>
              </Pressable>
            </VStack>
            <VStack
              style={{
                paddingHorizontal: vw(40),
                justifyContent: "center",
                flex: 1,
              }}
            >
              {property && tab === 0 && (
                <Info
                  _showAlert={_showAlert}
                  property={property}
                  statuslist={status}
                  tagslist={tagslist}
                  searching={searching}
                  setSearching={setSearching}
                  doSmartSearch={doSmartSearch}
                />
              )}
              {property && tab === 1 && (
                <Attributes _showAlert={_showAlert} property={property} />
              )}
              {property && tab === 2 && (
                <Campaign
                  _showAlert={_showAlert}
                  property={property}
                  searching={searching}
                  setSearching={setSearching}
                  doSmartSearch={doSmartSearch}
                />
              )}
            </VStack>
          </HStack>
        </VStack>
      ) : (
        <VStack h='full' flex={1}>
          <Spinner size={"sm"} />
        </VStack>
      )}
    </VStack>
  );
};

export default Details;
