import React, { useState } from "react";
import {
  Center,
  Box,
  Text,
  VStack,
  HStack,
  Pressable,
  ChevronDownIcon,
  ChevronRightIcon,
} from "native-base";
import _ from "lodash";

import { vw, vh } from "../../../util";

const Attributes = (props) => {
  const property = props.property;

  const [pKey] = useState([
    { category: "Parcel", key: "parcel", icon: "ios-home", active: true },
    {
      category: "Structure",
      key: "structure",
      icon: "ios-home",
      active: false,
    },
    { category: "Owner", key: "owner", icon: "ios-person", active: false },
    // { category: "Location", key:"coords", icon: "ios-person", active: false },
    { category: "Deeds", key: "deeds", icon: "ios-home", active: false },
    { category: "Taxes", key: "taxes", icon: "ios-home", active: false },
    {
      category: "Assessments",
      key: "assessments",
      icon: "ios-home",
      active: false,
    },
    {
      category: "Valuations",
      key: "valuation",
      icon: "ios-home",
      active: false,
    },
  ]);

  function isEmpty(obj) {
    for (var key in obj) {
      if (obj.hasOwnProperty(key)) return false;
    }
    return true;
  }

  const AttributeItem = ({ item, category }) => {
    const [active, setActive] = useState(category.active);
    const title = category.category.trim();

    const CategoryItem = ({ k, v }) => {
      // k = splitCC(k).join(" ");
      return (
        <HStack
          flex={1}
          style={{
            justifyContent: "flex-start",
            marginVertical: vh(12.5),
          }}
        >
          <Text
            style={{
              fontFamily: "Poppins",
              fontSize: vw(15),
              width: vw(350),
              color: "#303030",
            }}
          >
            {k}
          </Text>
          <Text
            style={{
              fontFamily: "Poppins_medium",
              fontSize: vw(15),
              color: "#303030",
              textAlign: "left",
              flexGrow: 1,
              flexWrap: "wrap",
            }}
          >
            {k === "Sale Price" ? v.replace("(", "\n(") : _.unescape(v)}
          </Text>
        </HStack>
      );
    };

    const keys = Object.keys(item);
    // if (title === "Taxes") {
    //   console.log("KEYS", keys);
    //   console.log("ACTIVE", active);
    //   console.log(item[0]);
    // }

    return (
      <VStack
        style={{
          backgroundColor: "white",
          borderRadius: vw(10),
          boxShadow: "0px 3px 10px #0000001A",
          paddingHorizontal: vw(20),
          marginBottom: vh(10),
        }}
      >
        <Pressable
          onPress={() => {
            setActive(!active);
          }}
        >
          <HStack
            style={{
              height: vh(54),
              justifyContent: "space-between",
              alignItems: "center",
              // marginHorizontal: vw(20),
              borderBottomWidth: active ? vw(2) : 0,
              borderBottomColor: "rgba(0,0,0, 0.10)",
            }}
          >
            <Text
              style={{
                fontFamily: "Poppins_medium",
                fontSize: vw(13),
                color: "#303030",
              }}
            >
              {title}
            </Text>

            <Center>
              {active ? (
                <ChevronDownIcon size={vw(12)} />
              ) : (
                <ChevronRightIcon size={vw(12)} />
              )}
            </Center>
          </HStack>
        </Pressable>
        {active && (
          <Box>
            {keys.map((k) => {
              const v = item[k];
              if (title === "Deeds") {
                // console.log("title === Deeds", Object.keys(v));
                if (typeof v === "object") {
                  return (
                    <Box
                      key={k}
                      mb={2}
                      // borderBottomColor={"coolGray.500"}
                      // borderBottomWidth={1}
                    >
                      {Object.keys(v).map((k) => {
                        const d = v[k];
                        return (
                          <Box key={k}>
                            <Text
                              textTransform={"capitalize"}
                              m={2}
                              fontSize={12}
                              fontWeight='bold'
                            >
                              {k}
                            </Text>
                            {Object.keys(d).map((dk) => {
                              // this is for Loan Org Object
                              if (typeof d[dk] === "object") {
                                return (
                                  <Box key={k}>
                                    <Text
                                      textTransform={"capitalize"}
                                      m={2}
                                      fontSize={10}
                                      fontWeight='bold'
                                    >
                                      {dk}
                                    </Text>
                                    {Object.keys(d[dk]).map((dkk) => {
                                      return (
                                        <CategoryItem
                                          key={dkk}
                                          k={dkk}
                                          v={d[dk][dkk]}
                                        />
                                      );
                                    })}
                                  </Box>
                                );
                              } else {
                                return (
                                  <CategoryItem key={dk} k={dk} v={d[dk]} />
                                );
                              }
                            })}
                          </Box>
                        );
                      })}{" "}
                    </Box>
                  );
                }
              } else {
                if (
                  title === "Taxes" ||
                  title === "Assessments" ||
                  title === "Valuations"
                ) {
                  return (
                    <Box
                      key={k}
                      mb={2}
                      // borderBottomColor={"coolGray.500"}
                      // borderBottomWidth={1}
                    >
                      {Object.keys(v).map((k) => {
                        const val = v[k];
                        return <CategoryItem key={k} k={k} v={val} />;
                      })}
                    </Box>
                  );
                }
              }

              return <CategoryItem key={k} k={k} v={v} />;
            })}
          </Box>
        )}
      </VStack>
    );
  };

  return (
    !isEmpty(property.attributes) &&
    pKey.map((item, idx) => {
      const ci = property.attributes[item.key];
      return ci && <AttributeItem key={"a-" + idx} item={ci} category={item} />;
    })
  );
};

export default Attributes;
