import React, { useState } from "react";
import { HStack, Text } from "native-base";
import { useForm } from "react-hook-form";
import { useDispatch } from "react-redux";

import Card from "./Card";
import Util from "../../../util";
import ShadowSelect from "../../../components/ShadowSelect";
import * as propertiesActions from "../../../store/actions/properties";
import NotesIcon from "../../../components/svg/NotesIcon";

const { vw, vh } = Util;

const Status = ({ property, _showAlert, statuslist }) => {
  const dispatch = useDispatch();
  const [isOpen, setIsOpen] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const {
    control,
    handleSubmit,
    formState: { errors },
    reset,
    getValues,
    trigger,
  } = useForm({
    mode: "all",
    defaultValues: {
      status: property && property.status ? property.status._id : "",
    },
  });

  const onSubmit = async (data) => {
    try {
      const selected = statuslist.find((s) => s._id === data.status);
      
      if (!selected) {
        _showAlert("error", "An error occurred.");
        return;
      }

      let postdata = {
        _id: property._id,
        status: { _id: selected._id, name: selected.name },
      };
      console.log(postdata);
      const action = propertiesActions.updatePropertyStatus(postdata);
      setIsLoading(true);
      await dispatch(action);
      setIsLoading(false);
      _showAlert("success", "Property status updated successfully.");
      setIsOpen(false);
    } catch (err) {
      _showAlert("error", err.message);
      setIsLoading(false);
    }
  };

  const statusOptions = statuslist
    ? statuslist.map((s) => ({
        label: s.name,
        value: s._id,
      }))
    : [];

  return (
    <>
      <Card
        title='Status'
        onPress={() => {
          setIsOpen(s => !s);
        }}
        rightElement={<NotesIcon />}
      >
        <HStack
          style={{
            justifyContent: "flex-start",
            marginVertical: vh(12.5),
          }}
        >
          <Text
            style={{
              fontFamily: "Poppins",
              fontSize: vw(15),
              width: vw(350),
              color: "#303030",
            }}
          >
            Current Status
          </Text>
          <Text
            style={{
              fontFamily: "Poppins_medium",
              fontSize: vw(15),
              color: "#303030",
              height: vh(30)
            }}
          >
            {!isOpen && property && property.status && property.status.name}
            {isOpen && (
              <ShadowSelect
                style={{marginTop: vh(-10)}}
                control={control}
                items={statusOptions}
                name='status'
                placeholder='Select status'
                onChange={() => {
                  trigger("status");
                  handleSubmit(onSubmit)();
                }}
                invalid={errors.Status ? true : false}
                error={errors.status ? errors.status.message : ""}
                rules={{
                  required: {
                    value: true,
                    message: "Please select a status.",
                  },
                }}
              />
            )}
          </Text>
        </HStack>
      </Card>
    </>
  );
};

export default Status;
