import React from "react";
import { VStack } from "native-base";

import {vw, vh} from "../../../util";
import Owner from "./Owner";
import Status from "./Status";
import Notes from "./Notes";
import Phones from "./Phones";
import Emails from "./Emails";
import Images from "./Images";
import Tags from "./Tags";
import Market from "./Market";



const Info = ({
  property,
  _showAlert,
  statuslist,
  tagslist,
  searching,
  setSearching,
  doSmartSearch,
}) => {

  return (
    <VStack space={4} flex={1} style={{marginBottom: vh(20)}}>
      <Owner _showAlert={_showAlert} property={property} />
      {property && property.mkt && (
        <Market
          _showAlert={_showAlert}
          property={property}
          statuslist={statuslist}
        />
      )}
      <Status
        _showAlert={_showAlert}
        property={property}
        statuslist={statuslist}
      />
      <Notes _showAlert={_showAlert} property={property} />
      <Phones
        _showAlert={_showAlert}
        property={property}
        searching={searching}
        setSearching={setSearching}
        doSmartSearch={doSmartSearch}
      />
      <Emails
        _showAlert={_showAlert}
        property={property}
        searching={searching}
        setSearching={setSearching}
        doSmartSearch={doSmartSearch}
      />
      <Tags _showAlert={_showAlert} property={property} tagslist={tagslist} />
      <Images _showAlert={_showAlert} property={property} />
    </VStack>
  );
};

export default Info;
