import React from "react";
import { VStack, HStack, Text, Pressable } from "native-base";
import AddIcon from "../../../components/svg/AddIcon";
import Util from "../../../util";
import Colors from "../../../constants/Colors";
const { vw, vh } = Util;
const Card = ({
  title,
  subtitle,
  children,
  onPress,
  rightElement,
  style,
  titlestyle,
  headerStyle,
}) => {
  return (
    <VStack
      style={{
        backgroundColor: "white",
        borderRadius: vw(10),
        boxShadow: "0px 3px 10px #0000001A",
        ...style,
      }}
    >
      {title && (
        <HStack
          style={{
            height: vh(54),
            justifyContent: "space-between",
            alignItems: "center",
            marginHorizontal: vw(20),
            borderBottomWidth: vw(2),
            borderBottomColor: "rgba(0,0,0, 0.10)",
            ...headerStyle,
          }}
        >
          <VStack space={1}>
            <Text
              style={{
                fontFamily: "Poppins_medium",
                fontSize: vw(13),
                color: "#303030",
                ...titlestyle,
              }}
            >
              {title}
            </Text>
            {subtitle && (
              <Text
                style={{
                  fontFamily: "Poppins_medium",
                  fontSize: vw(13),
                  color: "#303030",
                  marginTop: vh(-8)
                }}
              >
                {subtitle}
              </Text>
            )}
          </VStack>
          {onPress ? (
            <Pressable onPress={onPress}>
              {rightElement ? rightElement : <AddIcon fill={Colors.primary} />}
            </Pressable>
          ) : (
            rightElement
          )}
        </HStack>
      )}
      <VStack style={{ marginHorizontal: vw(20) }}>{children}</VStack>
    </VStack>
  );
};

export default Card;
