import React, { useState, useRef, useCallback } from "react";
import {
  FlatList,
  Pressable,
  Center,
  Spinner,
  Image,
  CheckCircleIcon,
} from "native-base";
import { useForm } from "react-hook-form";
import { useDispatch } from "react-redux";

import { CDN, getUser, API_URL } from "../../../constants/Config";
import Card from "./Card";
import { vw, vh } from "../../../util";
import * as propertiesActions from "../../../store/actions/properties";
import Colors from "../../../constants/Colors";
import * as events from "../../../store/events";

const Images = ({ property, _showAlert }) => {
  const uRef = useRef();
  const dispatch = useDispatch();
  const [isLoading, setIsLoading] = useState(false);
  const [editImage, setEditImage] = useState({});
  const [progress, setProgress] = useState(0);
  // const { fetchedProperty: property } = useSelector(
  //   (state) => state.properties
  // );
  const images = property && property.pics ? property.pics : [];

  const {
    formState: { errors },
  } = useForm({
    mode: "all",
    defaultValues: { ...editImage },
  });

  const setDefaultImage = async (data, pics, setIsLoading) => {
    try {
      setIsLoading(true);
      if (pics && pics.length === 0) {
        _showAlert("error", "Something went wrong.");
        return;
      }
      const action = propertiesActions.setDefaultImage(
        {
          pics: pics.map((p) => {
            p.default = p.id === data.id;
            return p;
          }),
          _id: property._id,
        },
        null
      );
      await dispatch(action);
      _showAlert("success", "Image is set default.");
      setIsLoading(false);
    } catch (err) {
      setIsLoading(false);
      _showAlert("error", err.message);
    }
  };

  const ImageItem = ({ data, property }) => {
    const [isLoading, setIsLoading] = useState(false);
    return (
      <Pressable
        style={{
          backgroundColor: "white",
          borderRadius: 10,
          // padding: 10,
          marginVertical: vh(10),
          borderColor: Colors.primary,
          // borderWidth: 1,
          marginRight: vw(10),
        }}
        onPress={() => {
          if (data.default === false) {
            setDefaultImage(data, property.pics, setIsLoading);
          }
        }}
      >
        {isLoading ? (
          <Center
            style={{
              width: vw(180),
              height: vh(100),
              // marginHorizontal: 5,
              borderRadius: 10,
              overflow: "hidden",
            }}
          >
            <Spinner size={"sm"} color={Colors.primary} />
          </Center>
        ) : (
          <>
            <Image
              style={{
                width: vw(180),
                height: vh(100),
                borderRadius: 10,
                overflow: "hidden",
              }}
              resizeMode={"cover"}
              // cacheKey={`pp-${property._id + "-" + data.id}`}
              source={{
                uri: `${CDN}/properties/${property._id}/${data.id}`,
              }}
              alt='Pic'
            />
            {data.default && (
              <Center
                rounded={"full"}
                style={{
                  position: "absolute",
                  right: -5,
                  top: -5,
                  // borderColor: "white",
                  // borderWidth: 1,
                  backgroundColor: "white",
                }}
              >
                <CheckCircleIcon color={Colors.primary} />
              </Center>
            )}
          </>
        )}
      </Pressable>
    );
  };

  const onChangeHandler = async () => {
    const { token } = getUser();
    // var file = uploadFile.current.files[0];
    setProgress(0);
    setIsLoading(true);
    for (const file of uRef.current.files) {
      // Allowed types
      var mime_types = ["image/jpeg", "image/jpg", "image/png"];

      // Validate MIME type
      if (mime_types.indexOf(file.type) === -1) {
        setIsLoading(false);
        _showAlert("error", "Incorrect file type.");
        return;
      }

      // Max 2 Mb allowed
      if (file.size > 2000 * 1024 * 1024) {
        setIsLoading(false);
        _showAlert("error", "Exceeded size 2GB.");
        return;
      }

      var data = new FormData();

      var request = new XMLHttpRequest();

      data.append("_id", property._id);
      data.append("default", false);
      data.append("picture", file);

      // AJAX request finished
      request.addEventListener("load", function (e) {
        setIsLoading(false);
      });

      // Upload progress on request.upload
      request.upload.addEventListener("progress", function (e) {
        var completed = (e.loaded / e.total) * 100;
        setProgress(completed);
        // setImageLoading(true);
      });

      request.onreadystatechange = async function () {
        if (this.readyState === 4 && this.status === 200) {
          const result = JSON.parse(this.responseText);
          if (result.success) {
            if (("request.onreadystatechange", result.data)) {
              await dispatch({
                type: events.FETCHED_PROPERTY,
                data: result.data,
              });
            }
            // setImages(result.data.pics);
          }
        } else {
          if (this.status > 200) {
            _showAlert("error", "Cannot add image, something went wrong.");
            // console.log(this.responseText);
            setIsLoading(false);
          }
          // throw new Error("something went wrong");
        }
      };
      // If server is sending a JSON response then set JSON response type
      request.responseType = "text";

      // Send POST request to the server side script
      request.open("POST", `${API_URL}/properties/upload/picture`, true);
      request.setRequestHeader("Authorization", "Bearer " + token);
      request.send(data);
    }
    uRef.current.value = "";
  };

  const renderItem = useCallback(
    ({ item }) => <ImageItem data={item} property={property} propertyId={property._id} />,
    [images.length]
  );

  return (
    <>
      <Card
        title='Images'
        onPress={() => {
          // setImgId("0");
          setProgress(0);
          uRef.current.click();
        }}
      >
        <FlatList
          horizontal={true}
          data={images}
          renderItem={renderItem}
          keyExtractor={(item, index) => item.id.toString()}
          scrollEnabled={images.length > 1}
          removeClippedSubviews={true}
        />
        <input
          ref={uRef}
          accept='image/jpg'
          name='imgupload'
          type='file'
          onChange={onChangeHandler}
          style={{ display: "none" }}
        />
      </Card>
    </>
  );
};

export default Images;
