import React, { useState } from "react";
import {
  VStack,
  Center,
  Text,
  View,
  CheckCircleIcon,
  CircleIcon,
  CloseIcon,
} from "native-base";
import moment from "moment-timezone";

import Card from "./Card";
import Colors from "../../../constants/Colors";
import Util from "../../../util";
const { vw, vh } = Util;

const Mailers = ({ property }) => {
  const [isOpen, setIsOpen] = useState(false);

  const history =
    property && property.mailers && property.mailers.postcards
      ? property.mailers.postcards
      : [];

  return (
    <Card
      title='Mail Activity'
      // onPress={() => {
      //   setIsOpen(true);
      // }}
    >
      <VStack
        style={{
          flexDirection: "column",
          marginHorizontal: vw(20),
          marginVertical: vh(40),
        }}
      >
        {history && history.length === 0 && (
          <Center m={4}>
            <Text
              style={{
                fontFamily: "Poppins",
                fontSize: vw(13),
                marginBottom: vh(10),
              }}
            >
              Attach a campaign to see mail activity.
            </Text>
          </Center>
        )}
        {history &&
          history.length > 0 &&
          history.map((item, idx) => {
            // const delivered = true;
            // console.log("item.events",item.events);
            // console.log(item.sent)
            const currDate = moment().format("YYYY-MM-DD");
            const delivered =
              item.sent && currDate >= item.sent.expected_delivery_date;
            const expected =
              (item.sent && item.sent.expected_delivery_date) || null;
            const failed = (item && item.sent && item.sent.error) || null;
            const status = delivered || expected || failed;
            return (
              <View
                key={idx}
                style={{
                  flexDirection: "row",
                }}
              >
                <View
                  style={{
                    borderColor: "transparent",
                    borderStartColor: Colors.primary,
                    borderWidth: 2,
                    paddingVertical: 20,
                    marginBottom: 13,
                  }}
                >
                  <Center
                    style={{
                      fontSize: vw(20),
                      color: Colors.primary,
                      marginLeft: vw(-10.5),
                      marginTop: vw(-40),
                      zIndex: 99,
                    }}
                  >
                    {(item.sent &&
                      item.sent.tracking_events &&
                      item.sent.tracking_events.length > 0) ||
                    (item.sent && item.sent.expected_delivery_date) ? (
                      <CheckCircleIcon color={Colors.primary} />
                    ) : item.sent && item.sent.error ? (
                      <CloseIcon color={Colors.primary} />
                    ) : (
                      <CircleIcon color={Colors.primary} />
                    )}
                  </Center>
                </View>
                <View style={{ marginBottom: 15, marginLeft: 18 }}>
                  <Text
                    style={{
                      fontSize: 13,
                      fontWeight: "700",
                      textAlign: "left",
                      color: Colors.primary,
                      marginTop: -17,
                    }}
                  >
                    {`${item.campaign} - step ${item.step} - mail ${item.mailer}`}
                  </Text>
                  <Text
                    style={{
                      fontSize: 14,
                      textAlign: "left",
                      color: "#414141",
                    }}
                  >
                    {item.sent &&
                    item.sent.tracking_events &&
                    item.sent.tracking_events.length > 0
                      ? item.sent.tracking_events.map((t) => {
                          // return capitalize(t.name) + " > ";
                          return t.name + " > ";
                        })
                      : item.sent &&
                        item.sent.tracking_events &&
                        item.sent.tracking_events.length === 0
                      ? !item.sent
                        ? "Scheduled @ " +
                          moment(item.date).utc().format("MM-DD-YYYY")
                        : "Scheduled @ " +
                          moment(item.sent.send_date)
                            .utc()
                            .format("MM-DD-YYYY HH:mm UTC")
                      : "Scheduled"}
                  </Text>
                  <Text
                    style={{
                      fontSize: 14,
                      textAlign: "left",
                      color: "#414141",
                    }}
                  >
                    {/* {!item.sent
                          ? moment(item.date).format("MM-DD-YYYY")
                          : moment(item.sent.send_date).format(
                              "MM-DD-YYYY HH:mm"
                            )} */}
                    {item && item.sent && item.sent.error && (
                      <Text
                        style={{
                          fontSize: 12,
                          textAlign: "left",
                          color: "red",
                        }}
                      >
                        {"\n" + item.sent.error.message}
                      </Text>
                    )}
                  </Text>
                  {status && (
                    <Text
                      style={{
                        fontSize: 14,
                        textAlign: "left",
                        color: "#414141",
                      }}
                    >
                      {delivered && "Delivered!"}
                      {expected && "Expected Delivery:"}
                      {failed && "Failed"}
                    </Text>
                  )}
                </View>
              </View>
            );
          })}
      </VStack>
    </Card>
  );
};

export default Mailers;
