import React, { useState } from "react";
import {
  VStack,
  Center,
  Text,
  View,
  CheckCircleIcon,
  CircleIcon,
} from "native-base";
import moment from "moment-timezone";

import Card from "./Card";
import ShadowButton from "../../../components/ShadowButton";
import Colors from "../../../constants/Colors";
import Util from "../../../util";
const { vw, vh } = Util;

const Emailers = ({ property, doSmartSearch, searching, campaignType }) => {
  const [isOpen, setIsOpen] = useState(false);

  const history =
    property && property.mailers && property.mailers.emails
      ? property.mailers.emails
      : [];

  return (
    <Card
      title='Email Activity'
      // onPress={() => {
      //   setIsOpen(true);
      // }}
    >
      <VStack
        style={{
          flexDirection: "column",
          marginHorizontal: vw(20),
          marginVertical: vh(40),
        }}
      >
        {history && history.filter((f) => f !== null).length === 0 && (
          <Center m={2}>
            {(!property.smartsearch &&
              property &&
              property.campaign &&
              property.campaign.name &&
              campaignType === "E-mail") ||
            campaignType === "Mail & E-mail" ? (
              <>
                <Text
                  style={{
                    fontFamily: "Poppins",
                    fontSize: vw(13),
                    marginBottom: vh(10),
                  }}
                >
                  Add an email address to get the email campaign attached.
                </Text>
                <ShadowButton
                  isLoading={searching}
                  label='Smart Search'
                  onPress={doSmartSearch}
                />
              </>
            ) : (
              <Text
                style={{
                  fontFamily: "Poppins",
                  fontSize: vw(13),
                  marginBottom: vh(10),
                }}
              >
                Attach a campaign to see email activity.
              </Text>
            )}
          </Center>
        )}
        {history &&
          history.length > 0 &&
          history
            .filter((f) => f !== null)
            .map((item, idx) => {
              return (
                <View
                  key={idx}
                  style={{
                    flexDirection: "row",
                  }}
                >
                  <View
                    style={{
                      borderColor: "transparent",
                      borderStartColor: Colors.primary,
                      borderWidth: vw(2),
                      paddingVertical: vh(20),
                      marginBottom: vh(13),
                    }}
                  >
                    <Center
                      style={{
                        fontSize: vw(20),
                        marginLeft: vw(-9),
                        marginTop: vw(-36),
                        zIndex: 99,
                      }}
                    >
                      {item && item.sent ? (
                        <CheckCircleIcon color={Colors.primary} />
                      ) : (
                        <CircleIcon color={Colors.primary} />
                      )}
                    </Center>
                  </View>
                  <View style={{ marginBottom: vh(15), marginLeft: vw(18) }}>
                    <Text
                      style={{
                        fontFamily: "Poppins_medium",
                        fontSize: vw(15),
                        textAlign: "left",
                        color: Colors.primary,
                        marginTop: vh(-17),
                      }}
                    >
                      {`${item.campaign} - step ${item.step} - email ${item.mailer}`}
                    </Text>
                    <Text
                      style={{
                        fontFamily: "Poppins",
                        fontSize: vw(13),
                        textAlign: "left",
                        color: "rgba(0,0,0, 0.5)",
                      }}
                    >
                      {item &&
                        !item.sent &&
                        "Scheduled @ " +
                          moment(item.date).utc().format("MM-DD-YYYY HH:mm") +
                          " UTC"}
                      {item &&
                        item.sent &&
                        item.sent.failed &&
                        "Failed @ " +
                          moment(item.sent_date)
                            .utc()
                            .format("MM-DD-YYYY HH:mm") +
                          " UTC"}
                      {item &&
                        item.sent &&
                        item.sent.delivered &&
                        "Delivered @ " +
                          moment(item.sent_date)
                            .utc()
                            .format("MM-DD-YYYY HH:mm") +
                          " UTC"}
                      {item && item.sent && item.sent.failed && (
                        <Text
                          style={{
                            fontFamily: "Poppins",
                            fontSize: vw(13),
                            textAlign: "left",
                            color: "red",
                          }}
                        >
                          {"\n" + item.sent.error}
                        </Text>
                      )}
                    </Text>
                  </View>
                </View>
              );
            })}
      </VStack>
    </Card>
  );
};

export default Emailers;
