import React from "react";
import { Select, FormControl } from "native-base";
import { Controller } from "react-hook-form";
import Util from "../util";
import Colors from "../constants/Colors";
import DropDownIcon from "./svg/DropDownIcon";
const { vw, vh } = Util;

const ShadowSelect = React.forwardRef((props, ref) => {
  return (
    <FormControl
      isInvalid={props.invalid}
      style={{
        height: vh(48),
        ...props.style,
      }}
    >
      {props.label && (
        <FormControl.Label
          style={{
            fontSize: vw(13),
            fontFamily: "Poppins_medium",
            color: props.invalid ? "red.500" : "rgba(102, 102, 102, 0.7)",
            height: vh(19),
          }}
        >
          {props.label}
        </FormControl.Label>
      )}

      <Controller
        control={props.control}
        rules={props.rules}
        name={props.name}
        render={({ field: { onChange, onBlur, value } }) => (
          <Select
            ref={ref}
            size={props.size || "lg"}
            dropdownIcon={<DropDownIcon />}
            _invalid={{
              borderColor: "red.300",
              borderWidth: "1",
            }}
            // _light={{
            //   borderWidth: "1",
            // }}
            // _dark={{
            //   borderWidth: "1",
            // }}
            // _web={{
            //   borderWidth: "1",
            // }}
            style={{
              fontSize: vw(15),
              fontFamily: "Poppins_medium",
              backgroundColor: "white",
              padding: vw(5),
              height: props.height || vh(36),
            }}
            placeholder={props.placeholder}
            selectedValue={value || props.defaultValue}
            onValueChange={(v) => {
              onChange(v);
              if (props.onChange) {
                props.onChange(v);
              }
            }}
          >
            {props.items &&
              props.items.length > 0 &&
              props.items.map((item, idx) => (
                <Select.Item
                  _pressed={{
                    borderWidth: 1,
                    borderColor: Colors.primary,
                    backgroundColor: "transparent",
                  }}
                  borderWidth={1}
                  borderColor='transparent'
                  borderRadius={50}
                  fontSize={16}
                  key={props.name + idx}
                  label={item.label}
                  value={item.value}
                />
              ))}
          </Select>
        )}
      />
      <FormControl.ErrorMessage style={{ marginTop: vh(-3) }}>
        {props.error}
      </FormControl.ErrorMessage>
    </FormControl>
  );
});

export default ShadowSelect;
