import React, { useState } from "react";
import { HStack, Text, VStack } from "native-base";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";

import Card from "./Card";
import { vw, vh } from "../../../util";
import ShadowModal from "../../../components/ShadowModal";
import ShadowSelect from "../../../components/ShadowSelect";
import ShadowButton from "../../../components/ShadowButton";
import * as propertiesActions from "../../../store/actions/properties";

const AttachCampaign = ({ property, _showAlert, campaignType }) => {
  const dispatch = useDispatch();
  const [isOpen, setIsOpen] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const { userCampaigns: campaigns } = useSelector((state) => state.campaigns);

  const campaignOptions =
    campaigns.length > 0
      ? campaigns.map((item) => {
          return { label: item.name, value: item._id };
        })
      : [];
  if (campaignOptions.length) {
    campaignOptions.unshift({ label: "No Campaign", value: "" });
  }
  const {
    control,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    mode: "all",
    defaultValues: {
      campaign: property && property.campaign && property.campaign._id,
    },
  });

  const onSubmit = async (data) => {
    try {
      setIsLoading(true);
      const action = propertiesActions.updateOutreachCampaignSettings({
        _id: property._id,
        campaign: campaigns.find((f) => f._id === data.campaign),
      });
      await dispatch(action);
      setIsLoading(false);
      _showAlert("success", "Campaign attached successfully.");
      setIsOpen(false);
    } catch (err) {
      _showAlert("error", err.message);
      setIsLoading(false);
    }
  };

  return (
    <>
      <Card
        title='Attached Campaign'
        onPress={() => {
          setIsOpen(true);
        }}
      >
        {property && property.campaign && property.campaign.name ? (
          <VStack>
            <HStack
              style={{
                justifyContent: "flex-start",
                marginVertical: vh(12.5),
              }}
            >
              <Text
                style={{
                  fontFamily: "Poppins",
                  fontSize: vw(15),
                  width: vw(350),
                  color: "#303030",
                }}
              >
                Name
              </Text>
              <Text
                style={{
                  fontFamily: "Poppins_medium",
                  fontSize: vw(15),
                  color: "#303030",
                }}
              >
                {property && property.campaign
                  ? property.campaign.createdby === property.campaign.team
                    ? property.campaign.name + " (Owner)"
                    : property.campaign.name + " (User)"
                  : "N/A"}
              </Text>
            </HStack>
            <HStack
              style={{
                justifyContent: "flex-start",
                marginVertical: vh(12.5),
              }}
            >
              <Text
                style={{
                  fontFamily: "Poppins",
                  fontSize: vw(15),
                  width: vw(350),
                  color: "#303030",
                }}
              >
                Type
              </Text>
              <Text
                style={{
                  fontFamily: "Poppins_medium",
                  fontSize: vw(15),
                  color: "#303030",
                }}
              >
                {campaignType}
              </Text>
            </HStack>
            <HStack
              style={{
                justifyContent: "flex-start",
                marginVertical: vh(12.5),
              }}
            >
              <Text
                style={{
                  fontFamily: "Poppins",
                  fontSize: vw(15),
                  width: vw(350),
                  color: "#303030",
                }}
              >
                Steps
              </Text>
              <Text
                style={{
                  fontFamily: "Poppins_medium",
                  fontSize: vw(15),
                  color: "#303030",
                }}
              >
                {property && property.campaign
                  ? property.campaign.steps.length
                  : ""}
              </Text>
            </HStack>
          </VStack>
        ) : (
          <Text style={{ marginVertical: vh(10) }}>No Campaign Attached</Text>
        )}
      </Card>
      <ShadowModal
        size='lg'
        header='Attach Campaign'
        height={vh(100)}
        bodyHeight={vh(100)}
        isOpen={isOpen}
        close={() => {
          reset();
          setIsOpen(false);
        }}
        buttons={
          <HStack space={2}>
            <ShadowButton
              space={2}
              label='Cancel'
              variant='outline'
              onPress={() => {
                reset();
                setIsOpen(false);
              }}
              style={{
                height: vh(36),
              }}
            />
            <ShadowButton
              space={2}
              isLoading={isLoading}
              label='Save'
              onPress={handleSubmit(onSubmit)}
              style={{
                height: vh(36),
              }}
            />
          </HStack>
        }
      >
        <ShadowSelect
          isRequired
          name='campaign'
          label={"Select a campaign "}
          placeholder='Select campaign'
          control={control}
          items={campaignOptions}
          rules={{
            required: true,
            message: "Please select a campaign",
          }}
          style={{ marginTop: vh(-20) }}
          invalid={errors.campaign ? true : false}
          error={errors.campaign ? errors.campaign.message : ""}
          inputStyle={{}}
        />
      </ShadowModal>
    </>
  );
};

export default AttachCampaign;
