import React from "react";
import { HStack, Text } from "native-base";

import Card from "./Card";
import Util, { percentDiff } from "../../../util";
import PriceUpIcon from "../../../components/svg/PriceUpIcon";
import PriceDownIcon from "../../../components/svg/PriceDownIcon";

const { vw, vh } = Util;

var formatter = new Intl.NumberFormat("en-US", {
  style: "currency",
  currency: "USD",
  minimumFractionDigits: 0,
  maximumFractionDigits: 0,
});

const Market = ({ property, _showAlert }) => {
  const mkt = property.mkt;
  const status =
    mkt.price === mkt.sold ? "no" : mkt.price > mkt.sold ? "high" : "low";
  const percent =
    mkt.sold === "-" || mkt.sold === 0 || !mkt.sold
      ? 0
      : percentDiff(mkt.sold, mkt.price);
  return (
    <>
      <Card title='On Market'>
        <HStack
          style={{
            justifyContent: "flex-start",
            marginVertical: vh(20),
          }}
        >
          <Text
            style={{
              fontFamily: "Poppins",
              fontSize: vw(15),
              width: vw(350),
              color: "#303030",
            }}
          >
            Estimate
          </Text>
          <HStack>
            <Text
              style={{
                fontFamily: "Poppins_medium",
                fontSize: vw(15),
                color: "#303030",
              }}
            >
              {mkt && mkt.price ? formatter.format(mkt.price) : "-"}
            </Text>
            {percent > 0 && (
              <HStack justifyContent={"flex-start"} alignItems={"center"}>
                {status === "high" && <PriceUpIcon />}
                {status === "low" && <PriceDownIcon />}
                <Text
                  style={{
                    fontSize: vw(13),
                    fontFamily: "Poppins",
                    color: status === "high" ? "#4fc567" : "#fb4949",
                  }}
                >
                  ({percent + "%"})
                </Text>
              </HStack>
            )}
          </HStack>
        </HStack>
        <HStack style={{ marginBottom: vh(10) }}>
          <Text
            style={{
              fontFamily: "Poppins",
              fontSize: vw(15),
              width: vw(350),
              color: "#303030",
            }}
          >
            Last Sale
          </Text>
          <Text
            style={{
              fontFamily: "Poppins_medium",
              fontSize: vw(15),
              color: "#303030",
            }}
          >
            {mkt && mkt.sold ? formatter.format(mkt.sold) : "-"}
          </Text>
        </HStack>
      </Card>
    </>
  );
};

export default Market;
