import React, { useState } from "react";
import {
  HStack,
  Text,
  FlatList,
  Pressable,
  Checkbox,
  VStack,
  Center,
  CheckCircleIcon,
} from "native-base";
import { useForm } from "react-hook-form";
import { useDispatch } from "react-redux";

import Card from "./Card";
import Util from "../../../util";
import ShadowModal from "../../../components/ShadowModal";
import ShadowInput from "../../../components/ShadowInput";
import ShadowPhone from "../../../components/ShadowPhone";
import ShadowButton from "../../../components/ShadowButton";
import * as propertiesActions from "../../../store/actions/properties";
import Colors from "../../../constants/Colors";
import { useRef } from "react";

const { vw, vh } = Util;

const Phones = ({
  property,
  _showAlert,
  searching,
  // setSearching,
  doSmartSearch,
}) => {
  const pRef = useRef();
  const dispatch = useDispatch();
  const [isOpen, setIsOpen] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [editPhone, setEditPhone] = useState({});
  const phones = property && property.phones ? property.phones : [];
  const {
    control,
    handleSubmit,
    formState: { errors },
    reset,
    getValues,
    setValue,
    trigger,
  } = useForm({
    mode: "all",
    defaultValues: { ...editPhone },
  });

  const onSubmit = async (data) => {
    try {
      let phone = {
        label: data.label,
        phone: data.phone,
      };

      let msg = data.label + " Phone number Saved successfully!"

      if (editPhone && editPhone.phone) {
        msg = data.label + " Phone number updated successfully!"
        phone.edit = editPhone.phone;
        phone.smartsearch = editPhone.smartsearch;

        if (data.default) {
          phone.default = true;
          const dp = phones.find((p) => p.default === true);
          if (dp && dp.phone !== phone.edit) {
            phone.defaultPhone = dp.phone;
          }
        }
      } else {
        if (phones && phones.length === 0) {
          phone.default = true;
        }
      }

      let postdata = {
        _id: property._id,
        phones: [phone],
      };

      const action = propertiesActions.savePhoneNumbers(postdata);
      setIsLoading(true);

      await dispatch(action);
      _showAlert("success", msg);
      setIsLoading(false);
      reset();
      setIsOpen(false);
    } catch (err) {
      _showAlert("error", err.message);
      setIsLoading(false);
    }
  };

  let formatPhoneNumber = (str) => {
    //Filter only numbers from the input
    let cleaned = ("" + str).replace(/\D/g, "");

    //Check if the input is of correct
    let match = cleaned.match(/^(1|)?(\d{3})(\d{3})(\d{4})$/);

    if (match) {
      //Remove the matched extension code
      //Change this to format for any country code.
      let intlCode = match[1] ? "+1 " : "";
      return [intlCode, "(", match[2], ") ", match[3], "-", match[4]].join("");
    }

    return null;
  };

  const PhoneItem = (props) => {
    return (
      <Pressable
        onHoverIn={() => {}}
        onHoverOut={() => {}}
        onPress={() => {
          if (props.data.default && props.data.smartsearch) {
            return;
          }

          setIsOpen(true);
          // onOpen();
          // setEditWhat("phone");
          setValue("phone", props.phone);
          setValue("label", props.label);
          setValue("default", props.data.default);
          setEditPhone(props.data);
          // setSelectedPhone(props.phone);
        }}
        _web={{
          backgroundColor: "white",
        }}
        style={{
          borderRadius: 10,
          height: vh(48),
          margin: vh(10),
          paddingTop: vh(5),
          borderColor: Colors.primary,
          borderWidth: 1,
          paddingHorizontal: vw(10),
        }}
      >
        <Text
          style={{
            fontFamily: "Poppins_medium",
            fontSize: vw(13),
            color: "#303030",
          }}
        >
          {formatPhoneNumber(props.phone)}
        </Text>
        <Text
          style={{
            fontFamily: "Poppins",
            fontSize: vw(11),
            color: "#303030",
          }}
        >
          {props.label}
        </Text>
        {props.data.default && (
          <Center
            rounded={"full"}
            style={{
              position: "absolute",
              right: -5,
              top: -5,
              // borderColor: "white",
              // borderWidth: 1,
              backgroundColor: "white",
            }}
          >
            <CheckCircleIcon color={Colors.primary} />
          </Center>
        )}
      </Pressable>
    );
  };

  return (
    <VStack>
      <Card
        title='Phones'
        onPress={() => {
          setEditPhone({});
          reset();
          setIsOpen(true);
        }}
      >
        <FlatList
          ListEmptyComponent={
            !property.smartsearch ? null : (
              <Text
                style={{
                  marginVertical: vh(10),
                }}
              >
                No phone numbers found with Smart Search
              </Text>
            )
          }
          scrollEnabled={phones.length > 1}
          ListHeaderComponent={
            !property.smartsearch ? (
              <ShadowButton
                onPress={doSmartSearch}
                isLoading={searching}
                label='Smart Search'
                style={{ marginVertical: vh(10) }}
              />
            ) : null
          }
          // style={{ marginHorizontal: 12 }}
          horizontal={true}
          data={phones.filter((p) => p.phone !== "")}
          renderItem={({ item }) => {
            return (
              <PhoneItem
                data={item}
                phone={item.phone}
                label={item.label}
                propertyId={property._id}
                smartsearch={item.smartsearch}
              />
            );
          }}
          keyExtractor={(item, index) => index.toString()}
        />
      </Card>

      <ShadowModal
        size='lg'
        header='Phone Number'
        bodyHeight={vh(80)}
        isOpen={isOpen}
        close={() => {
          reset();
          setIsOpen(false);
        }}
        buttons={
          <HStack space={2}>
            <ShadowButton
              space={2}
              color={"rgba(0,0,0, 0.5 )"}
              label='Cancel'
              variant='outline'
              onPress={() => {
                reset();
                setIsOpen(false);
              }}
            />
            <ShadowButton
              space={3}
              isLoading={isLoading}
              label='Save'
              onPress={handleSubmit(onSubmit)}
            />
          </HStack>
        }
      >
        <HStack
          style={{
            justifyContent: "space-between",
            zIndex: 9999,
            marginTop: vh(-20),
          }}
          space={4}
        >
          <ShadowInput
            isReadOnly={editPhone && editPhone.smartsearch}
            style={{ width: "48%", marginLeft: 1 }}
            isRequired
            name='label'
            label={"Label "}
            placeholder='Enter phone label'
            control={control}
            rules={{
              required: true,
              message: "Please enter phone label",
            }}
            invalid={errors.label ? true : false}
            error={errors.label ? errors.label.message : ""}
            inputStyle={{}}
          />

          <ShadowPhone
            isReadOnly={editPhone && editPhone.smartsearch}
            style={{ width: "48%" }}
            ref={pRef}
            control={control}
            isRequired
            name='phone'
            label='Phone Number '
            placeholder='Phone'
            initialValue={
              editPhone && editPhone.phone
                ? editPhone.phone.replace(/ /g, "").replace("+", "")
                : ""
            }
            invalid={errors.phone ? true : false}
            error={errors.phone ? errors.phone.message : ""}
            rules={{
              required: {
                value: true,
                message: "Please enter a valid phone number.",
              },
            }}
          />
        </HStack>
        {!editPhone.default && (
          <HStack
            style={{
              marginTop: vh(20),
              justifyContent: "flex-start",
              alignItems: "center",
              zIndex: 9999,
              // marginVertical: vh(10),
              marginHorizontal: vh(2),
              // height: vh(50),
              paddingVertical: vh(10),
            }}
          >
            <Checkbox
              name='default'
              onChange={(v) => {
                console.log(v);
                setValue("default", v);
                trigger("default");
              }}
              isChecked={getValues("default") || false}
            />

            <Text
              style={{
                marginLeft: vw(10),
                fontSize: vw(15),
                fontFamily: "Poppins",
                color: "rgba(0,0,0,0.8)",
              }}
            >
              Default
            </Text>
          </HStack>
        )}
      </ShadowModal>
    </VStack>
  );
};

export default Phones;
