import React, { useState } from "react";
import {
  HStack,
  Text,
  VStack,
  Box,
  CircleIcon,
  Pressable,
  Spinner,
  Center,
} from "native-base";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";

import Card from "./Card";
import Util from "../../../util";
import ShadowModal from "../../../components/ShadowModal";
import ShadowInput from "../../../components/ShadowInput";
import ShadowButton from "../../../components/ShadowButton";
import * as propertiesActions from "../../../store/actions/properties";
import Colors from "../../../constants/Colors";

const { vw, vh } = Util;

const Tags = ({ property, _showAlert }) => {
  const dispatch = useDispatch();
  const tagslist = useSelector((state) => state.properties.tags);
  const [isOpen, setIsOpen] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const [selectedTag, setSelectedTag] = useState(property.tags);
  const {
    control,
    handleSubmit,
    formState: { errors },
    reset,
    getValues,
  } = useForm({
    mode: "all",
    defaultValues: {
      postcardname:
        property && property.postcardname
          ? property.postcardname
          : property.name,
    },
  });

  const tags = property ? property.tags : [];

  const Tag = ({ item }) => {
    const selected = selectedTag.find((f) => f._id === item._id) ? true : false;

    return (
      <Pressable
        onPress={() => {
          setSelectedTag((p) => {
            if (selected) {
              return p.filter((i) => i._id !== item._id);
            } else {
              return p.concat({ _id: item._id, name: item.name });
            }
          });

          // props.navigation.navigate("PropertyList", {
          //   tag: item.id,
          //   tagname: item.name,
          // });
        }}
        style={{
          backgroundColor: !selected ? "#9B9B9B" : Colors.primary,
          borderRadius: 24,
          margin: 5,
          alignSelf: "flex-start",
        }}
      >
        <Box
          style={{
            flexDirection: "row",
            justifyContent: "center",
            alignItems: "center",
            paddingVertical: 7,
            paddingHorizontal: 10,
          }}
        >
          <CircleIcon style={{ fontSize: 12, color: "white" }} />
          <Text
            style={{
              fontFamily: "Poppins",
              fontSize: vw(14),
              color: "white",
              alignSelf: "flex-start",
              marginLeft: 5,
              marginTop: -2,
            }}
          >
            {" " + item.name}
          </Text>
        </Box>
      </Pressable>
    );
  };

  const TagItem = ({ data }) => {
    return (
      <Box
        style={{
          flexDirection: "row",
          justifyContent: "center",
          alignItems: "center",
          paddingVertical: 7,
          paddingHorizontal: 10,
          backgroundColor: Colors.primary,
          margin: 5,
          borderRadius: 24,
        }}
      >
        <CircleIcon style={{ fontSize: 12, color: "white" }} />
        <Text
          style={{
            fontFamily: "Poppins",
            fontSize: vw(13),
            color: "white",
            alignSelf: "flex-start",
            marginLeft: vw(5),
          }}
        >
          {" " + data.name}
        </Text>
      </Box>
    );
  };

  const save = async () => {
    try {
      setIsSaving(true);
      await dispatch(
        propertiesActions.updatePropertyTag({
          _id: property._id,
          tags: selectedTag || [],
        })
      );
      _showAlert("success", "Property tags updated.");
      setIsSaving(false);
      setIsOpen(false);
    } catch (err) {
      _showAlert("error", err.message);
      setIsSaving(false);
    }
  };

  const onSubmit = async (data) => {
    try {
      const action = propertiesActions.savePropertyTag({
        name: data.name,
        default: false,
      });
      setIsLoading(true);
      await dispatch(action);
      await dispatch(propertiesActions.fetchTags());
      setIsLoading(false);
      _showAlert("success", "Tag created successfully.");
      // setIsOpen(false);
      reset();
    } catch (err) {
      _showAlert("error", err.message);
      setIsLoading(false);
    }
  };

  return (
    <>
      <Card
        title='Tags'
        onPress={() => {
          setIsOpen(true);
        }}
      >
        <VStack
          style={{
            flex: 1,
            marginTop: 5,
            marginBottom: 16,
            paddingTop: 5,
          }}
        >
          <Text
            style={{
              fontFamily: "Poppins_medium",
              fontSize: vw(13),
              color: "rgba(0,0,0, 0.8)",
            }}
          >
            What do you notice when looking at the property?
          </Text>
          <HStack
            style={{
              flexDirection: "row",
              justifyContent: "flex-start",
              alignItems: "flex-start",
              flexWrap: "wrap",
            }}
          >
            {tags &&
              tags.length > 0 &&
              tags &&
              tags.map((item, idx) => (
                <TagItem key={idx.toString()} data={item} />
              ))}
          </HStack>
        </VStack>
      </Card>
      <ShadowModal
        size='lg'
        header='Tags'
        isOpen={isOpen}
        close={() => {
          reset();
          setIsOpen(false);
        }}
        buttons={
          <HStack space={2}>
            <ShadowButton
              space={2}
              color={"rgba(0,0,0, 0.5 )"}
              label='Cancel'
              variant='outline'
              onPress={() => {
                reset();
                setIsOpen(false);
              }}
            />
            <ShadowButton
              space={3}
              isLoading={isSaving}
              label='Save'
              onPress={async () => {
                save();
              }}
            />
          </HStack>
        }
      >
        <HStack>
          <ShadowInput
            isRequired
            name='name'
            // label={"Tag Name "}
            placeholder='Enter tag name'
            control={control}
            rules={{
              required: true,
              message: "Please enter tag name",
            }}
            style={{ marginTop: vh(-10) }}
            invalid={errors.name ? true : false}
            error={errors.name ? errors.name.message : ""}
            inputStyle={{}}
            rightIcon={<></>}
            onSubmitEditing={() => {
              handleSubmit(onSubmit)();
            }}
          />
          {isLoading ? (
            <Center style={{ marginLeft: vw(-30), marginTop: vh(-15) }}>
              <Spinner />
            </Center>
          ) : (
            <Pressable
              onPress={() => {
                // console.log("here");
                handleSubmit(onSubmit)();
              }}
              style={{
                zIndex: 999,
              }}
            >
              <Text
                style={{
                  fontFamily: "Poppins_medium",
                  fontSize: vw(15),
                  color: Colors.primary,
                  // marginHorizontal: vw(10),
                  marginLeft: vw(-40),
                }}
              >
                ADD
              </Text>
            </Pressable>
          )}
        </HStack>
        <HStack
          flexWrap={"wrap"}
          justifyContent={"flex-start"}
          marginTop={vh(10)}
        >
          {tagslist &&
            tagslist.length > 0 &&
            tagslist.map((tag) => {
              return <Tag key={tag._id} item={tag} defaultTag={true} />;
            })}
        </HStack>
      </ShadowModal>
    </>
  );
};

export default Tags;
