import React from "react";
import { VStack } from "native-base";

import Address from "./Address";
import AttachCampaign from "./AttachCampaign";
import Emailers from "./Emailers";
import Mailers from "./Mailers";
import Util from "../../../util";
const { vw, vh } = Util;

const Campaign = ({
  _showAlert,
  property,
  searching,
  setSearching,
  doSmartSearch,
}) => {
  let campaignType = "-";
  const pt =
    property &&
    property.campaign &&
    property.campaign.steps &&
    property.campaign.steps.find((s) => s.postcardtemplate);
  if (pt) {
    campaignType = "Mail";
  }
  const et =
    property &&
    property.campaign &&
    property.campaign.steps &&
    property.campaign.steps.find((s) => s.emailtemplate);

  if (et) {
    campaignType =
      campaignType === "Mail" ? campaignType + " & E-mail" : "E-mail";
  }
  return (
    <VStack space={4} flex={1} marginBottom={vh(40)}>
      <AttachCampaign
        _showAlert={_showAlert}
        property={property}
        campaignType={campaignType}
      />
      <Address _showAlert={_showAlert} property={property} />
      {campaignType !== "E-mail" && (
        <Mailers
          _showAlert={_showAlert}
          property={property}
          searching={searching}
          setSearching={setSearching}
          doSmartSearch={doSmartSearch}
          campaignType={campaignType}
        />
      )}
      {campaignType !== "Mail" && (
        <Emailers
          _showAlert={_showAlert}
          property={property}
          searching={searching}
          setSearching={setSearching}
          doSmartSearch={doSmartSearch}
          campaignType={campaignType}
        />
      )}
    </VStack>
  );
};

export default Campaign;
