import React, { useState } from "react";
import { HStack, Text, VStack } from "native-base";
import { useForm } from "react-hook-form";
import { useDispatch } from "react-redux";

import Card from "./Card";
import Util from "../../../util";
import ShadowModal from "../../../components/ShadowModal";
import ShadowInput from "../../../components/ShadowInput";
import ShadowButton from "../../../components/ShadowButton";
import * as propertiesActions from "../../../store/actions/properties";
import NotesIcon from "../../../components/svg/NotesIcon";

const { vw, vh } = Util;

const Owner = ({ property, _showAlert }) => {
  const dispatch = useDispatch();
  const [isOpen, setIsOpen] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const {
    control,
    handleSubmit,
    formState: { errors },
    reset,
    getValues,
  } = useForm({
    mode: "all",
    defaultValues: {
      postcardname:
        property && property.postcardname
          ? property.postcardname
          : property.name,
    },
  });

  const onSubmit = async (data) => {
    try {
      let postdata = {
        _id: property._id,
        postcardname: data.postcardname,
      };
      const action = propertiesActions.updatePostCardName(postdata);
      setIsLoading(true);
      await dispatch(action);
      setIsLoading(false);
      _showAlert("success", "Owner information updated successfully.");
      setIsOpen(false);
    } catch (err) {
      _showAlert("error", err.message);
      setIsLoading(false);
    }
  };

  return (
    <>
      <Card
        title='Owner'
        onPress={() => {
          setIsOpen(true);
        }}
        rightElement={<NotesIcon />}
      >
        <HStack
          style={{
            justifyContent: "flex-start",
            marginVertical: vh(12.5),
          }}
        >
          <Text
            style={{
              fontFamily: "Poppins",
              fontSize: vw(15),
              width: vw(350),
              color: "#303030",
            }}
          >
            Name
          </Text>
          <Text
            style={{
              fontFamily: "Poppins_medium",
              fontSize: vw(15),
              color: "#303030",
            }}
          >
            {property && property.name}
          </Text>
        </HStack>
        {property && property.postcardname && (
          <HStack
            style={{
              justifyContent: "flex-start",
              marginVertical: vh(12.5),
            }}
          >
            <Text
              style={{
                fontFamily: "Poppins",
                fontSize: vw(15),
                width: vw(350),
                color: "#303030",
              }}
            >
              Postcard Name
            </Text>
            <Text
              style={{
                fontFamily: "Poppins_medium",
                fontSize: vw(15),
                color: "#303030",
              }}
            >
              {property && property.postcardname}
            </Text>
          </HStack>
        )}
        <HStack
          style={{
            justifyContent: "flex-start",
            marginVertical: vh(12.5),
          }}
        >
          <Text
            style={{
              fontFamily: "Poppins",
              fontSize: vw(15),
              width: vw(350),
              color: "#303030",
            }}
          >
            Gender
          </Text>
          <Text
            style={{
              fontFamily: "Poppins_medium",
              fontSize: vw(15),
              color: "#303030",
            }}
          >
            {property && property.gender ? property.gender : "N/A"}
          </Text>
        </HStack>
        <HStack
          style={{
            justifyContent: "flex-start",
            marginVertical: vh(12.5),
          }}
        >
          <Text
            style={{
              fontFamily: "Poppins",
              fontSize: vw(15),
              width: vw(350),
              color: "#303030",
            }}
          >
            Age
          </Text>
          <Text
            style={{
              fontFamily: "Poppins_medium",
              fontSize: vw(15),
              color: "#303030",
            }}
          >
            {property && property.age_range ? property.age_range : "N/A"}
          </Text>
        </HStack>
      </Card>
      <ShadowModal
        placement='top'
        size={"lg"}
        header='Owner Information'
        desc='Edit this to change the appearance of the owner name on your
            postcard.'
        isOpen={isOpen}
        close={() => {
          reset();
          setIsOpen(false);
        }}
        buttons={
          <HStack space={2}>
            <ShadowButton
              space={2}
              label='Cancel'
              variant='outline'
              onPress={() => {
                reset();
                setIsOpen(false);
              }}
            />
            <ShadowButton
              space={3}
              isLoading={isLoading}
              label='Save'
              onPress={handleSubmit(onSubmit)}
            />
          </HStack>
        }
      >
        <VStack style={{height: vh(50)}}>
          <ShadowInput
            isRequired
            name='postcardname'
            label={"Owner Name "}
            placeholder='Enter owner name'
            control={control}
            rules={{
              required: true,
              message: "Please enter owner name",
            }}
            style={{ marginTop: vh(-20) }}
            invalid={errors.postcardname ? true : false}
            error={errors.postcardname ? errors.postcardname.message : ""}
            inputStyle={{}}
          />
        </VStack>
      </ShadowModal>
    </>
  );
};

export default Owner;
